import { useCallback, useEffect, useState, useRef } from "react";
import { API_BASE_URL } from '../lib/openai';

export interface Msg {
  sender: "user" | "ai" | "system";
  text: string;
  avatar?: string;
  characterName?: string;
  characterProfileImg?: string;
  characterAge?: number;
  characterJob?: string;
  name?: string;
  age?: number | string;
  job?: string;
  timestamp?: string;
}

export function useChat(characterId: string, personaId: string) {
  const [messages, setMessages] = useState<Msg[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [favor, setFavor] = useState(0);
  const [error, setError] = useState<string | null>(null);
  const retryCountRef = useRef(0);
  const lastMessageIdRef = useRef<string | null>(null);
  const MAX_RETRIES = 2;

  //   
  const clearError = useCallback(() => {
    setError(null);
  }, []);

  //    (    )
  useEffect(() => {
    if (!characterId || !personaId) return;
    
    const controller = new AbortController();
    let isMounted = true;
    
    const loadMessages = async () => {
      try {
        clearError();
        const response = await fetch(
          `${API_BASE_URL}/api/chat/${characterId}?personaId=${personaId}`,
          { 
            signal: controller.signal,
            headers: {
              'Cache-Control': 'no-cache',
              'Content-Type': 'application/json'
            }
          }
        );
        
        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`HTTP ${response.status}: ${errorText || '  '}`);
        }
        
        const data = await response.json();
        
        if (!isMounted) return;
        
        if (data.ok) {
          const formattedMessages = (data.messages || []).map((msg: any) => ({
            sender: msg.sender,
            text: msg.message,
            characterName: msg.characterName,
            characterProfileImg: msg.characterProfileImg,
            characterAge: msg.characterAge,
            characterJob: msg.characterJob,
            timestamp: msg.timestamp || msg.createdAt
          }));
          
          setMessages(formattedMessages);
          setFavor(data.favor || 0);
          
          if (data.fallback) {
            console.warn("   .");
          }
        } else {
          throw new Error(data.error || "  .");
        }
      } catch (err: any) {
        if (err.name === 'AbortError') return;
        
        console.error("  :", err);
        
        if (isMounted) {
          setError(err.message || "    .");
          //     
          setMessages([{
            sender: "system",
            text: "    .     ."
          }]);
        }
      }
    };

    loadMessages();
    
    return () => {
      isMounted = false;
      controller.abort();
    };
  }, [characterId, personaId, clearError]);

  //    (    )
  const sendMessage = useCallback(async (message: string) => {
    if (!message.trim() || loading) return;
    
    const messageText = message.trim();
    setInput("");
    setLoading(true);
    clearError();
    
    //   (   )
    const userMessage: Msg = {
      sender: "user",
      text: messageText,
      timestamp: new Date().toISOString()
    };
    
    setMessages(prev => [...prev, userMessage]);
    
    //  
    const attemptSend = async (attempt: number): Promise<void> => {
      try {
        const response = await fetch(`${API_BASE_URL}/api/chat`, {
          method: "POST",
          headers: { 
            "Content-Type": "application/json",
            'Cache-Control': 'no-cache'
          },
          body: JSON.stringify({
            characterId,
            personaId,
            message: messageText,
            sender: "user"
          }),
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`HTTP ${response.status}: ${errorText || '  '}`);
        }

        const data = await response.json();

        if (data.ok && data.aiText) {
          const aiMessage: Msg = {
            sender: "ai",
            text: data.aiText,
            timestamp: data.timestamp || new Date().toISOString()
          };
          
          setMessages(prev => [...prev, aiMessage]);
          
          //  
          if (data.favorDelta && data.favorDelta !== 0) {
            setFavor(prev => Math.max(0, Math.min(100, prev + data.favorDelta)));
          }
          
          retryCountRef.current = 0; //    
        } else {
          throw new Error(data.error || "AI   .");
        }
      } catch (err: any) {
        console.error(`   ${attempt + 1} :`, err);
        
        if (attempt < MAX_RETRIES) {
          console.log(` ... (${attempt + 1}/${MAX_RETRIES})`);
          await new Promise(resolve => setTimeout(resolve, 1000 * (attempt + 1))); //  
          return attemptSend(attempt + 1);
        }
        
        //     
        const errorMessage: Msg = {
          sender: "system",
          text: `  : ${err.message || '   '}.    .`
        };
        
        setMessages(prev => [...prev, errorMessage]);
        setError(err.message || "  .");
      }
    };

    try {
      await attemptSend(0);
    } finally {
      setLoading(false);
    }
  }, [characterId, personaId, loading, clearError]);

  //    (  )
  const clearChat = useCallback(async () => {
    if (!window.confirm("    ?")) return;
    
    try {
      clearError();
      const response = await fetch(`${API_BASE_URL}/api/chat`, {
        method: "DELETE",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ personaId, characterId }),
      });

      if (response.ok) {
        setMessages([]);
        setFavor(0);
      } else {
        const errorText = await response.text();
        throw new Error(` : ${errorText || ' '}`);
      }
    } catch (err: any) {
      console.error("  :", err);
      setError(err.message || "  .");
    }
  }, [personaId, characterId, clearError]);

  //  
  const refreshMessages = useCallback(() => {
    setMessages([]);
    clearError();
    // useEffect   
  }, [clearError]);

  return {
    messages,
    input,
    setInput,
    sendMessage,
    loading,
    favor,
    error,
    clearError,
    clearChat,
    refreshMessages,
    hasError: !!error
  };
}
