import React, { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import DefaultProfileImage from "../components/DefaultProfileImage";
import Toast from "../components/Toast";
import { useAuth } from "../hooks/useAuth";
import { API_BASE_URL } from '../lib/openai';

const hashtags = [
  "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#", "#"
];

const categories = [
  " &  ", " ", " ", "", " &  ", "", ""
];

export default function CharacterCreatePage() {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [gender, setGender] = useState(" ");
  const [scope, setScope] = useState("");
  const [roomCode, setRoomCode] = useState("");
  const [category, setCategory] = useState("");
  const [selectedTags, setSelectedTags] = useState<string[]>([]);
  const [profileImg, setProfileImg] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [extraInfos, setExtraInfos] = useState<string[]>([]);
  const [name, setName] = useState("");
  const [age, setAge] = useState("");
  const [job, setJob] = useState("");
  const [oneLiner, setOneLiner] = useState("");
  const [background, setBackground] = useState("");
  const [personality, setPersonality] = useState("");
  const [habit, setHabit] = useState("");
  const [like, setLike] = useState("");
  const [dislike, setDislike] = useState("");
  const [firstScene, setFirstScene] = useState("");
  const [firstMessage, setFirstMessage] = useState("");
  const [backgroundImg, setBackgroundImg] = useState<string | null>(null);
  const backgroundImgInputRef = useRef<HTMLInputElement>(null);
  const [toast, setToast] = useState<{ message: string; type?: "success" | "error" } | null>(null);
  const [loading, setLoading] = useState(false);
  const [showSuccessModal, setShowSuccessModal] = useState<{ id: string } | null>(null);

  const handleProfileImgClick = () => {
    fileInputRef.current?.click();
  };
  const handleProfileImgChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (ev) => {
        setProfileImg(ev.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAddExtraInfo = () => {
    if (extraInfos.length < 10) {
      setExtraInfos([...extraInfos, ""]);
    }
  };
  const handleRemoveExtraInfo = (idx: number) => {
    setExtraInfos(extraInfos.filter((_, i) => i !== idx));
  };
  const handleChangeExtraInfo = (idx: number, value: string) => {
    setExtraInfos(extraInfos.map((info, i) => (i === idx ? value : info)));
  };

  const handleBackgroundImgClick = () => {
    if (backgroundImg) return;
    backgroundImgInputRef.current?.click();
  };
  const handleBackgroundImgChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (ev) => {
        setBackgroundImg(ev.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };
  const handleRemoveBackgroundImg = () => setBackgroundImg(null);

  const handleSubmit = async () => {
    if (!profileImg) {
      setToast({ message: "  .", type: "error" });
      return;
    }
    if (!name.trim()) {
      setToast({ message: "  .", type: "error" });
      return;
    }
    if (name.trim().length < 2) {
      setToast({ message: "  2  .", type: "error" });
      return;
    }
    if (!age || parseInt(age) < 1 || parseInt(age) > 150) {
      setToast({ message: "  . (1-150)", type: "error" });
      return;
    }
    if (!job.trim()) {
      setToast({ message: " .", type: "error" });
      return;
    }
    if (!oneLiner.trim()) {
      setToast({ message: "   .", type: "error" });
      return;
    }
    if (!background.trim()) {
      setToast({ message: "  .", type: "error" });
      return;
    }
    if (!personality.trim()) {
      setToast({ message: " .", type: "error" });
      return;
    }
    if (!firstScene.trim()) {
      setToast({ message: "  .", type: "error" });
      return;
    }
    if (!firstMessage.trim()) {
      setToast({ message: "   .", type: "error" });
      return;
    }
    
    setLoading(true);
    
    try {
      const payload = {
        userId: user?.uid || "",
        profileImg,
        name: name.trim(),
        age: parseInt(age),
        job: job.trim(),
        oneLiner: oneLiner.trim(),
        background: background.trim(),
        personality: personality.trim(),
        habit: habit.trim(),
        like: like.trim(),
        dislike: dislike.trim(),
        extraInfos: extraInfos.filter(info => info.trim()),
        gender,
        scope,
        roomCode: roomCode.trim(),
        category,
        selectedTags,
        firstScene: firstScene.trim(),
        firstMessage: firstMessage.trim(),
        backgroundImg,
      };
      
      const response = await fetch(`${API_BASE_URL}/api/character`, {
        method: "POST",
        headers: { 
          "Content-Type": "application/json",
          "Accept": "application/json"
        },
        body: JSON.stringify(payload),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
      const data = await response.json();
      
      if (data.ok && data.id) {
        if (data.message) {
          setToast({ message: data.message, type: "success" });
        }
        
        if (data.fallback) {
          setToast({ 
            message: "  .    .", 
            type: "success" 
          });
        }
        
        setShowSuccessModal({ id: data.id.toString() });
      } else {
        throw new Error(data.error || '  .');
      }
    } catch (error: any) {
      console.error('Character creation error:', error);
      
      const errorMessage = error.message?.includes('HTTP') 
        ? "   .    ."
        : error.message || "    .";
        
      setToast({ message: errorMessage, type: "error" });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ background: "var(--color-bg)", minHeight: "100vh", paddingBottom: 80 }}>
      {/*  */}
      <div style={{ display: 'flex', alignItems: 'center', padding: "24px 20px 0 20px" }}>
        <button 
          onClick={() => navigate(-1)} 
          style={{ 
            background: 'none', 
            border: 'none', 
            color: '#fff', 
            fontSize: 28, 
            marginRight: 12,
            cursor: 'pointer',
            padding: 4
          }}
          aria-label=""
        >
          &larr;
        </button>
        <span style={{ fontWeight: 700, fontSize: 24 }}> </span>
      </div>
      {/*    */}
      <div style={{ display: "flex", flexDirection: "column", alignItems: "center", marginTop: 24, marginBottom: 8 }}>
        <div style={{ fontWeight: 700, fontSize: 16, marginBottom: 8 }}>
            <span style={{ color: 'var(--color-point)', fontWeight: 700, fontSize: 14 }}>()</span>
        </div>
        <div style={{ position: "relative", width: 90, height: 90 }}>
          {profileImg ? (
            <img
              src={profileImg}
              alt=""
              style={{ width: 90, height: 90, borderRadius: "50%", objectFit: "cover", border: "2px solid var(--color-border)" }}
            />
          ) : (
            <DefaultProfileImage />
          )}
          <button
            type="button"
            onClick={handleProfileImgClick}
            style={{
              position: "absolute",
              right: 0,
              bottom: 0,
              background: "var(--color-point)",
              color: "#fff",
              border: "none",
              borderRadius: "50%",
              width: 32,
              height: 32,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              fontSize: 22,
              cursor: "pointer",
              boxShadow: "0 2px 8px rgba(0,0,0,0.08)"
            }}
            aria-label="  "
          >
            +
          </button>
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            style={{ display: "none" }}
            onChange={handleProfileImgChange}
          />
        </div>
      </div>
      {/*   */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}>  </div>
        <input placeholder="" value={name} onChange={e => setName(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 12, background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} maxLength={15} />
        <input type="number" placeholder="" value={age} onChange={e => setAge(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 12, background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} min="0" max="150" />
        <input placeholder="" value={job} onChange={e => setJob(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 12, background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} maxLength={15} />
        <input placeholder="  " value={oneLiner} onChange={e => setOneLiner(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 12, background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} maxLength={80} />
        <textarea placeholder=", , MBTI,   " value={background} onChange={e => setBackground(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 16, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} rows={3} maxLength={700} />
        <textarea placeholder=" " value={personality} onChange={e => setPersonality(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 16, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} rows={3} maxLength={700} />
        <textarea placeholder="   ( )" value={habit} onChange={e => setHabit(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 16, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} rows={3} maxLength={700} />
      </div>
      {/*   */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}>  </div>
        <textarea placeholder="  ()" value={like} onChange={e => setLike(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 16, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} rows={2} maxLength={50} />
        <textarea placeholder="  ()" value={dislike} onChange={e => setDislike(e.target.value)} style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 16, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }} rows={2} maxLength={50} />
        {extraInfos.map((info, idx) => (
          <div key={idx} style={{ display: "flex", alignItems: "center", marginBottom: 8 }}>
            <input
              type="text"
              value={info}
              onChange={e => handleChangeExtraInfo(idx, e.target.value)}
              placeholder={`  ${idx + 1}`}
              maxLength={50}
              style={{ flex: 1, borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginRight: 8, background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }}
            />
            <button type="button" onClick={() => handleRemoveExtraInfo(idx)} style={{ background: "none", border: "none", color: "var(--color-point)", fontSize: 20, cursor: "pointer" }}></button>
          </div>
        ))}
        <button
          type="button"
          onClick={handleAddExtraInfo}
          disabled={extraInfos.length >= 10}
          style={{ width: "100%", background: "var(--color-card-alt)", border: "none", borderRadius: 12, padding: 12, fontWeight: 600, color: extraInfos.length >= 10 ? "#ccc" : "var(--color-point)", marginBottom: 8, cursor: extraInfos.length >= 10 ? "not-allowed" : "pointer" }}
        >
          +    ({extraInfos.length}/10)
        </button>
      </div>
      {/*    */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}>  </div>
        <label><input type="radio" checked={scope === ""} onChange={() => setScope("")} /> </label>
        <label style={{ marginLeft: 16 }}><input type="radio" checked={scope === ""} onChange={() => setScope("")} /> </label>
      </div>
      {/*   */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}> </div>
        <div style={{ display: "flex", flexWrap: "wrap", gap: 8 }}>
          {hashtags.map(tag => (
            <button
              key={tag}
              onClick={() => setSelectedTags(selectedTags.includes(tag) ? selectedTags.filter(t => t !== tag) : [...selectedTags, tag])}
              style={{
                background: selectedTags.includes(tag) ? "var(--color-point)" : "var(--color-card-alt)",
                color: selectedTags.includes(tag) ? "#fff" : "var(--color-text)",
                border: "none",
                borderRadius: 16,
                padding: "6px 16px",
                fontWeight: 600,
                fontSize: 15,
                cursor: "pointer"
              }}
            >{tag}</button>
          ))}
        </div>
      </div>
      {/*   */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}> </div>
        {categories.map(cat => (
          <button
            key={cat}
            onClick={() => setCategory(cat)}
            style={{
              width: "100%",
              background: category === cat ? "var(--color-point)" : "var(--color-card-alt)",
              color: category === cat ? "#fff" : "var(--color-text)",
              border: "none",
              borderRadius: 24,
              padding: "12px 0",
              fontWeight: 600,
              fontSize: 17,
              marginBottom: 12,
              cursor: "pointer"
            }}
          >{cat}</button>
        ))}
      </div>
      {/*  ,  ,   */}
      <div style={{ background: "var(--color-card)", borderRadius: 20, margin: 20, padding: 20 }}>
        <div style={{ fontWeight: 700, fontSize: 18, marginBottom: 12 }}>   </div>
        <div style={{ marginBottom: 16 }}>
          <div style={{ fontWeight: 700, fontSize: 16 }}>  <span style={{ color: 'var(--color-point)', fontWeight: 700, fontSize: 14 }}>()</span></div>
          <textarea
            placeholder=") ,     "
            value={firstScene}
            onChange={e => setFirstScene(e.target.value.slice(0, 800))}
            style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 4, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }}
            rows={3}
            maxLength={800}
          />
          <div style={{ color: 'var(--color-subtext)', fontSize: 13, textAlign: 'right' }}>{firstScene.length}/800</div>
        </div>
        <div style={{ marginBottom: 16 }}>
          <div style={{ fontWeight: 700, fontSize: 16 }}>   <span style={{ color: 'var(--color-point)', fontWeight: 700, fontSize: 14 }}>()</span></div>
          <textarea
            placeholder=")     3  "
            value={firstMessage}
            onChange={e => setFirstMessage(e.target.value.slice(0, 800))}
            style={{ width: "100%", borderRadius: 12, border: "1px solid var(--color-border)", padding: 12, fontSize: 15, marginBottom: 4, resize: "none", background: "var(--color-card)", color: "var(--color-text)", boxSizing: 'border-box' }}
            rows={3}
            maxLength={800}
          />
          <div style={{ color: 'var(--color-subtext)', fontSize: 13, textAlign: 'right' }}>{firstMessage.length}/800</div>
        </div>
        <div style={{ marginBottom: 8 }}>
          <div style={{ fontWeight: 700, fontSize: 16 }}>  <span style={{ color: 'var(--color-subtext)', fontWeight: 400, fontSize: 14 }}>(, 9:16  )</span></div>
          <div style={{ color: 'var(--color-subtext)', fontSize: 13, marginBottom: 8 }}>       .</div>
          <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
            {backgroundImg ? (
              <div style={{ position: "relative" }}>
                <img src={backgroundImg} alt="" style={{ width: 120, height: 213, borderRadius: 12, objectFit: "cover", border: "1px solid var(--color-border)" }} />
                <button type="button" onClick={handleRemoveBackgroundImg} style={{ position: "absolute", top: -8, right: -8, background: "var(--color-point)", color: "#fff", border: "none", borderRadius: "50%", width: 22, height: 22, fontSize: 14, cursor: "pointer" }}></button>
              </div>
            ) : (
              <button type="button" onClick={handleBackgroundImgClick} style={{ width: 120, height: 213, borderRadius: 12, border: "1.5px dashed var(--color-point)", background: "var(--color-card-alt)", display: "flex", alignItems: "center", justifyContent: "center", fontSize: 32, color: "var(--color-point)", cursor: "pointer", position: 'relative' }}>
                <span style={{
                  background: "var(--color-point)",
                  color: "#fff",
                  borderRadius: "50%",
                  width: 48,
                  height: 48,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: 32,
                  position: 'absolute',
                  left: '50%',
                  top: '50%',
                  transform: 'translate(-50%, -50%)'
                }}>+</span>
              </button>
            )}
            <input ref={backgroundImgInputRef} type="file" accept="image/*" style={{ display: "none" }} onChange={handleBackgroundImgChange} />
          </div>
        </div>
      </div>
      {/*   */}
      <button
        onClick={handleSubmit}
        style={{
          width: "90%",
          margin: "24px 5% 32px 5%",
          background: "var(--color-point)",
          color: "#fff",
          border: "none",
          borderRadius: 32,
          padding: "18px 0",
          fontWeight: 700,
          fontSize: 20,
          boxShadow: "0 2px 8px rgba(0,0,0,0.08)",
          cursor: "pointer"
        }}
        disabled={loading}
      ></button>
      {loading && (
        <div style={{
          position: 'fixed', left: 0, top: 0, width: '100vw', height: '100vh', background: 'rgba(0,0,0,0.35)', zIndex: 9999, display: 'flex', alignItems: 'center', justifyContent: 'center'
        }}>
          <div style={{ background: '#222', color: '#fff', borderRadius: 18, padding: '18px 32px', fontSize: 20, fontWeight: 600, display: 'flex', alignItems: 'center', gap: 12 }}>
            <span> </span>
            <span className="dots-indicator"></span>
          </div>
          <style>{`
            .dots-indicator {
              display: inline-block;
              font-size: 22px;
              letter-spacing: 2px;
              animation: chat-dots-blink 1.2s infinite steps(3);
            }
            @keyframes chat-dots-blink {
              0% { opacity: 1; }
              33% { opacity: 0.5; }
              66% { opacity: 0.2; }
              100% { opacity: 1; }
            }
          `}</style>
        </div>
      )}
      {showSuccessModal && (
        <div style={{
          position: 'fixed', left: 0, top: 0, width: '100vw', height: '100vh', background: 'rgba(0,0,0,0.45)', zIndex: 10000, display: 'flex', alignItems: 'center', justifyContent: 'center'
        }}>
          <div style={{ background: '#fff', borderRadius: 22, padding: '40px 28px 32px 28px', minWidth: 260, maxWidth: 320, boxShadow: '0 4px 24px rgba(0,0,0,0.18)', textAlign: 'center', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div style={{ fontWeight: 700, fontSize: 22, color: '#ff4081', marginBottom: 24 }}> !</div>
            <button
              style={{ background: '#ff4081', color: '#fff', border: 'none', borderRadius: 16, padding: '16px 0', fontWeight: 700, fontSize: 18, width: '100%', cursor: 'pointer', marginTop: 8 }}
              onClick={() => navigate(`/character/${showSuccessModal.id}`)}
            > </button>
          </div>
        </div>
      )}
      {toast && (
        <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />
      )}
    </div>
  );
} 